import React, { useEffect } from 'react';
import { Container } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';

import KeysList from 'src/components/KeysList';
import { KeyPreviewModal } from 'src/components/KeyPreview';
import { useContainerKeys } from 'src/containers/Keys/hooks';

export const ArchivePage: React.FC = () => {
  const styles = useStyles();
  useHooks();

  return (
    <Container className={styles.container}>
      <KeysList archive />
      <KeyPreviewModal />
    </Container>
  );
};

const useStyles = makeStyles(() => ({
  container: {
    display: 'flex',
    minWidth: 0,
    flexGrow: 1,
    paddingTop: 16,
  },
}));

const useHooks = () => {
  const { fetchArchiveListWatch, resetList, routeParser, routeWatcher } = useContainerKeys();

  useEffect(() => {
    routeParser();
  }, [routeParser]);

  useEffect(() => {
    routeWatcher();
  }, [routeWatcher]);

  useEffect(() => {
    const req = fetchArchiveListWatch();
    return () => {
      req.abort();
    };
  }, [fetchArchiveListWatch]);

  useEffect(() => {
    return () => {
      resetList();
    };
  }, [resetList]);
};

export default ArchivePage;
