import fetcher from 'src/lib/utils/fetch';
import { IApiParamsAuthAccount, IApiResultAuthAccount } from './types';

export default async (params: IApiParamsAuthAccount): Promise<IApiResultAuthAccount | null> => {
  const result = await fetcher.fetchJson<IApiResultAuthAccount>(
    '{api}/auth/account',
    {
      method: 'POST',
      body: JSON.stringify(params)
    }
  );

  return result;
};
