import fetcher from 'src/lib/utils/fetch';
import { IApiResultKeysCreate, IApiParamsKeysCreate } from './types';

export default async (params: IApiParamsKeysCreate): Promise<IApiResultKeysCreate> => {
  const result = await fetcher.fetchJson<IApiResultKeysCreate['key']>(
    '{api}/entry/create',
    {
      method: 'POST',
      body: JSON.stringify(params),
    }
  );

  return { key: result };
};
