import fetcher from 'src/lib/utils/fetch';
import { IApiKeyEntry } from 'src/lib/api/types';

export interface IApiParamsKeysArchive {
  id: number;
}

export interface IApiResultKeysArchive {
  entry: IApiKeyEntry;
}

export default async (params: IApiParamsKeysArchive): Promise<IApiResultKeysArchive> => {
  const result = await fetcher.fetchJson<IApiResultKeysArchive['entry']>(
    `{api}/entry/archive/${params.id.toString()}`,
    {
      method: 'POST',
    }
  );

  return { entry: result };
};
