import { createStore, applyMiddleware, Middleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import createSagaMiddleware from 'redux-saga';
import thunk from 'redux-thunk';
import { createLogger } from 'redux-logger';

import rootReducer, { IStore } from './reducer';
import rootSaga from './saga';
import { useDispatch } from 'react-redux';
import { ThunkDispatch, AnyAction, PayloadAction, SerializedError } from '@reduxjs/toolkit';
import { IS_DEV } from './constants/settings';

// eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
export function initializeStore(initialState = {}) {
  // ======================================================
  // Middleware Configuration
  // ======================================================
  const sagaMiddleware = createSagaMiddleware();
  const middleware: Middleware[] = [thunk, sagaMiddleware];

  if (IS_DEV) {
    middleware.push(createLogger({ collapsed: true }));
  }

  // ======================================================
  // Enhancer Configuration
  // ======================================================
  const enhancer = applyMiddleware(...middleware);

  // ======================================================
  // Store Instantiation
  // ======================================================
  const result = createStore(rootReducer, initialState, composeWithDevTools(enhancer));

  sagaMiddleware.run(rootSaga);

  return result;
}

export const store = initializeStore();
export type AppDispatch = ThunkDispatch<IStore, void, AnyAction>;
export const useAppDispatch = (): AppDispatch => useDispatch();

export type IDispatchReturn<R, P> =
  | { error: SerializedError }
  | PayloadAction<
      R,
      string,
      {
        arg: P;
        requestId: string;
      },
      never
    >;
