import 'whatwg-fetch';
import { HttpRequestError } from '../errors/HttpResponseError';
import { API_URL } from 'src/constants/settings';
import { store } from 'src/store';
import { actions as sessionActions } from 'src/containers/Session/reducer';

const targetApi = API_URL;

class AppFetcher {
  headers: Record<string, string>;

  constructor(headers?: Record<string, string>) {
    this.headers = headers || {};
  }

  setHeader(prop: string, val: string) {
    this.headers = {
      ...this.headers,
      [prop]: val
    }
  }

  removeHeader(prop: string) {
    if (this.headers) {
      delete this.headers[prop];
    }
  }

  async fetch(input: RequestInfo, init?: RequestInit): Promise<Response> {
    let processedInput;

    if (typeof input !== 'string') {
      input.url.replace('{api}', targetApi);

      processedInput = {
        ...input,
        url: input.url,
      };
    } else {
      processedInput = input.replace('{api}', targetApi);
    }

    const headers = {
      'Content-Type': 'application/json',
      ...this.headers,
      ...init?.headers,
    };

    return window.fetch(processedInput, {
      credentials: 'include',
      ...init,
      headers,
    });
  }

  async fetchJson<T>(
    input: RequestInfo,
    init?: RequestInit,
  ): Promise<T> {
    try {
      const res = await this.fetch(input, init);

      if (res.status < 300) {
        return (await res.json()) as T;
      } else {
        const data = (await res.json()) as { statusCode: number; message: string };
        throw new HttpRequestError(data.statusCode, data.message);
      }
    } catch (err) {
      if (err instanceof HttpRequestError) {
        switch (err.status) {
          case 401:
            store.dispatch(sessionActions.setSessionExpired({ isExpired: true }));
            break;
          default:
        }
      }

      throw err;
    }
  }
}

export default new AppFetcher();
