import fetcher from 'src/lib/utils/fetch';
import { IApiKeyEntry } from 'src/lib/api/types';

export interface IApiParamsKeysRestore {
  id: number;
}

export interface IApiResultKeysRestore {
  entry: IApiKeyEntry;
}

export default async (params: IApiParamsKeysRestore): Promise<IApiResultKeysRestore> => {
  const result = await fetcher.fetchJson<IApiResultKeysRestore['entry']>(
    `{api}/entry/restore/${params.id.toString()}`,
    {
      method: 'POST',
    }
  );

  return { entry: result };
};
