import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';
import { Formik, Form, Field, FieldProps, FormikErrors, FormikProps } from 'formik';

import Message from 'src/components/Message';
import { errors as userServerErrors } from 'src/containers/Session/selectors';

import * as S from './styled';
import { TextField, CardContent, Card, Typography, CardHeader } from '@material-ui/core';
import { useContainerSession } from 'src/containers/Session/hooks';

export interface IUserLoginData {
  username: string;
  password: string;
}

const initialValues = { username: '', password: '' };

const LoginForm: React.FC = () => {
  const serverErrors = useSelector(userServerErrors);
  const { handleSubmit, validate } = useHooks();

  return (
    <Card>
      <CardHeader title={<Typography variant="h3">Sign in</Typography>} />
      <CardContent>
        <S.Container>
          <S.Body>
            <Formik
              initialValues={initialValues}
              validate={validate}
              onSubmit={handleSubmit}
            >
              {(_props: FormikProps<IUserLoginData>) => {
                return (
                  <Form>
                    <S.BodyLine>
                      <Field name="username">
                        {(params: FieldProps<string>) => {
                          const { field, meta } = params;
                          return (
                            <TextField
                              error={Boolean(meta.touched && meta.error)}
                              label="Login"
                              variant="outlined"
                              autoComplete="current-login"
                              {...field}
                            />
                          );
                        }}
                      </Field>
                    </S.BodyLine>
                    <S.BodyLine>
                      <Field name="password">
                        {(params: FieldProps<string>) => {
                          const { field, meta } = params;
                          return (
                            <TextField
                              label="Password"
                              variant="outlined"
                              type="password"
                              autoComplete="current-password"
                              error={Boolean(meta.touched && meta.error)}
                              {...field}
                            />
                          );
                        }}
                      </Field>
                    </S.BodyLine>
                    {serverErrors.loginForm && (
                      <S.BodyLine>
                        <Message>{serverErrors.loginForm}</Message>
                      </S.BodyLine>
                    )}
                    <S.BodyLine>
                      <S.LineItem>
                        <S.SubmitButton
                          color="secondary"
                          size="large"
                          type="submit"
                          variant="contained"
                        >
                          Войти
                        </S.SubmitButton>
                      </S.LineItem>
                    </S.BodyLine>
                  </Form>
                );
              }}
            </Formik>
          </S.Body>
        </S.Container>
      </CardContent>
    </Card>
  );
};

const useHooks = () => {
  const { fetchLoginUser } = useContainerSession();

  const handleSubmit = useCallback(
    async (values: IUserLoginData) => {
      await fetchLoginUser(values);
    },
    [fetchLoginUser],
  );

  const validate = useCallback((values: IUserLoginData) => {
    const { username, password } = values;
    const errors: FormikErrors<typeof values> = {};

    if (!username || username.length === 0) {
      errors.username = 'Username required';
    }

    if (!password || password.length === 0) {
      errors.password = 'Password required';
    }

    return errors;
  }, []);


  return {
    validate,
    handleSubmit,
  };
};

export default LoginForm;
