import React, { useEffect } from 'react';
import { Container } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import ReposList from 'src/components/ReposList';
import { useContainerRepos } from 'src/containers/Repos/hooks';
import { RepositoryFormModal } from 'src/components/RepositoryForm';

const useStyles = makeStyles(() => ({
  container: {
    paddingTop: 16,
    paddingBottom: 24,
  },
}));

export const ReposPage: React.FC = () => {
  const styles = useStyles();
  useHooks();

  return (
    <Container className={styles.container}>
      <ReposList />
      <RepositoryFormModal />
    </Container>
  );
};

const useHooks = () => {
  const { fetchListRepoWatch } = useContainerRepos();

  useEffect(() => {
    const req = fetchListRepoWatch();
    return () => {
      req.abort();
    };
  }, [fetchListRepoWatch]);
};
export default ReposPage;
