import fetcher from 'src/lib/utils/fetch';
import { IApiParamsAuthAccounts, IApiResultAuthAccounts } from './types';

export default async (_params?: IApiParamsAuthAccounts): Promise<IApiResultAuthAccounts> => {
  const result = await fetcher.fetchJson<IApiResultAuthAccounts>(
    '{api}/auth/accounts',
    {
      method: 'GET',
    }
  );

  return result;
};
