import fetcher from 'src/lib/utils/fetch';
import { IApiResultAuthLogin } from './types';
import { HttpRequestError } from 'src/lib/errors/HttpResponseError';

// eslint-disable-next-line @typescript-eslint/require-await
export default async (): Promise<IApiResultAuthLogin> => {
  const rawAuth = localStorage.getItem('auth');

  if (rawAuth === null) {
    throw new HttpRequestError(401, 'No jwt presents')
  }

  // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
  const result: IApiResultAuthLogin = JSON.parse(rawAuth);
  fetcher.setHeader(`Authorization`, `Bearer ${result.token}`);

  return result;
};
