import qs from 'qs';
import fetcher from 'src/lib/utils/fetch';
import { IApiPager, IApiStorageItem } from 'src/lib/api/types';

export interface IApiParamsKeysStorage {
  page?: number;
  perPage?: number;
  search?: string;
}

export interface IApiResultKeysStorage {
  items: IApiStorageItem[];
  pager: IApiPager;
}

export default async (params: IApiParamsKeysStorage): Promise<IApiResultKeysStorage> => {
  const query = qs.stringify(params, { addQueryPrefix: true });
  return await fetcher.fetchJson<IApiResultKeysStorage>(`{api}/key/storage${query}`, {
    method: 'GET',
  });
};
