import React from 'react';
import { renderRoutes, RouteConfigComponentProps } from 'react-router-config';

import TopBar from 'src/components/TopBar';
import ExpiredSession from 'src/components/ExpiredSession';
import MainMenu from 'src/components/MainMenu';

import * as S from './styled';

export interface IPropsDashboardLayout extends RouteConfigComponentProps {
}

const DashboardLayout: React.FC<IPropsDashboardLayout> = (props) => {
  const { route } = props;
  return (
    <S.Wrap>
      <ExpiredSession />
      <S.Header>
        <TopBar />
      </S.Header>
      <S.Body>
        <S.Menu>
          <MainMenu />
        </S.Menu>
        <S.Main>
          {route && route.routes && renderRoutes(route.routes)}
        </S.Main>
      </S.Body>
    </S.Wrap>
  );
};

export default DashboardLayout;
